

.row{
    margin-top:5px ;
    
    color: rgb(255, 255, 255);

}


.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-top:-10px ;
    
}



.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
}

.smallposter{
    cursor: pointer;
    max-height: 150px;
    margin-right: 10px;
}

.poster:hover{
    transform: scale(1.1);
    transition: all 1.7s ease;
    
}

.smallposter:hover{
    transform: scale(1.1);
    transition: all 0.9s ease-in-out;
}

h6{
    margin-left: 5px;
}