.banner{
    
    background-size: cover;
    height: 556px;
    color: rgb(255, 255, 255);
    
}

.content{
    padding-top: 140px;
    height: 299px;
    padding-left: 15px;

}

.title{
    font-size: 3rem;
    font-weight: 8000;
    padding-bottom: 0.3rem;
}

.button{
    color: rgb(255, 255, 255);
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: #111;
    background-color: #e6e6e6a9;
    
}

.discription{
    width: 60rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}

.fade{
    height: 8.5rem;
    background-image: linear-gradient(180deg,transparent,rgba(3, 0, 0, 0.788), #111);

}
.fadeup{
    height: 8.5rem;
    background-image: linear-gradient(180deg,transparent,rgba(3, 0, 0, 0.788), #111);

}